<template>
  <transition name="tratra">
    <div class="position-relative">
      <div v-if="showModal" class="mdl1">
        <div class="square"></div>
        <ul>
          <li>
            <button class="acc_ctrl" @click="showConfirm = !showConfirm">
              <img :src="require('@/assets/trash.svg')" alt="trash" />
              <p>Vymazat recept</p>
            </button>
            <hr />
            <transition name="slide">
              <div v-if="showConfirm" class="text-center">
                <p class=" py-2">Opravdu chcete recept vymazat?</p>
                <div class="d-flex justify-content-center pb-1">
                  <button
                    class="c-btn remove-revert"
                    @click="showConfirm = false"
                  >
                    Ne
                  </button>
                  <button
                    class="c-btn remove-submit"
                    @click="showConfirm = removeRecipe(recipe._id)"
                  >
                    Ano
                  </button>
                </div>
                <hr />
              </div>
            </transition>
          </li>
          <!-- <hr /> -->
          <li class="d-flex">
            <button class="acc_ctrl">
              <img :src="require('@/assets/pen.svg')" alt="pen" />
              <p>Upravit recept</p>
            </button>
          </li>
        </ul>
      </div>
      <div class="crd-header">
        <h3 class="food-title">{{ recipe.title }}</h3>
        <button class="menu" @click="showModal = !showModal">
          <img :src="require('@/assets/menu.svg')" alt="menu" />
        </button>
      </div>
      <img
        class="food-image"
        :src="`${staticURL}${recipe.image}`"
        alt="recipeImage"
      />
      <div class="crd-footer">
        <div class="autor-container w33">
          <img
            class="profile-photo"
            :src="
              require('@/assets/profile_photos/' + recipe.author.avatar_img)
            "
            alt=""
          />
          <div class="text-box">
            <p class="author-name">{{ recipe.author.username }}</p>
            <small class="posted-date">{{
              recipe.dateTime | moment('LLL')
            }}</small>
          </div>
        </div>
        <div class="w33 text-center">
          <p class="ft-1">Kategorie:</p>
          <p class="ft-2">Hlavní jídlo</p>
        </div>
        <div class="w33 text-right">
          <p class="ft-1">Doba přípravy:</p>
          <p class="ft-2">90min</p>
        </div>
      </div>
      <hr v-if="recipe.comments.length > 0" />
      <div v-if="openForComments === recipe._id" class="comment-box">
        <comment
          v-for="comment in recipe.comments"
          :key="comment.dateTime"
          :comment="comment"
          :recipe="recipe"
        ></comment>
        <hr />
        <div class="add-comment-box">
          <img :src="userAvatar" alt="userAvatar" class="add-comment-avatar" />
          <input
            type="text"
            placeholder="Přidejte komentář..."
            class="add-comment-input"
            v-model="commentInput"
            @keyup.enter="addComment(recipe._id)"
          />
          <button
            type="submit"
            class="btn btn-primary ml-auto"
            :disabled="commentInput === ''"
            @click="addComment(recipe._id)"
          >
            Zveřejnit
          </button>
        </div>
        <hr />
      </div>
      <div class="crd-footer-2">
        <a>Libí se mi</a>
        <a @click="$emit('clickComment', recipe._id)">Komentovat</a>
        <a>Přidat do mých receptů</a>
      </div>
    </div>
  </transition>
</template>

<script>
import Comment from '@/components/Comment.vue';
import ApiService from '@/services/ApiService';

export default {
  name: 'Recipe',

  components: { Comment },
  props: ['recipe', 'openForComments', 'userAvatar', 'recipes'],
  data() {
    return {
      commentInput: '',
      showModal: false,
      showConfirm: false,
      staticURL: `${process.env.VUE_APP_API_URL}/images/`,
    };
  },
  methods: {
    addComment(recipeId) {
      const data = { comment: this.commentInput };
      ApiService.addComment(localStorage.token, recipeId, data).then(
        (response) => {
          const newComment = response.data;
          const [commentedRecipe] = this.recipes.filter(
            (recipe) => recipe._id === recipeId, // eslint-disable-line
          );
          commentedRecipe.comments.push(newComment);

          const payload = {
            recipeId: commentedRecipe._id, // eslint-disable-line
            comment: newComment,
          };
          this.$emit('newCommentAdded', payload);
          this.commentInput = '';
        },
      );
    },
    removeRecipe(recipeId) {
      this.showConfirm = false;
      setTimeout(() => {
        this.showModal = false;
        setTimeout(() => {
          ApiService.removeRecipe(localStorage.token, recipeId).then(
            this.$emit('recipeRemoved', recipeId),
          );
        }, 50);
        console.log(`mažu recept ${recipeId}`);
      }, 250);
    },
  },
};
</script>

