<template>
  <div>
    <!-- <form @submit.prevent="uploadToServer"> -->
    <!-- <button @click="active = false">zavri</button> -->
    <div>
      <div class="crd-header">
        <h3 v-if="active" class="food-title">{{ name }}</h3>
      </div>

      <div class="px-3">
        <form
          v-if="!active"
          class="form-inline"
          @submit.prevent="active = true"
        >
          <h5 class="pb-2">Přidání nového receptu:</h5>
          <input
            v-model="name"
            type="text"
            class="form-control col-md-11"
            id="recipeName"
            aria-describedby="Název receptu"
            placeholder="Zadejte název receptu"
            autocomplete="off"
          />
          <button type="submit" class="btn btn-primary col-md-1">
            OK
          </button>
        </form>
      </div>
    </div>
    <div class="image-upload">
      <label for="recipeImageFile">
        <!-- <img :src="previewImage" alt="uploaded image" /> -->
        <PhotoHolder
          :class="['food-image', { 'food-image-active': active }]"
          :photo="this.previewImage"
        ></PhotoHolder>
        <!--         <img
          :src="imageUrl"
          alt="placeholder"
          :class="['food-image', { 'food-image-active': active }]"
        /> -->
      </label>
      <input
        type="file"
        class="form-control-file"
        id="recipeImageFile"
        @change="fileSelected"
      />
    </div>
    <!--   <div class="form-group"></div> -->
    <div v-if="active" class="crd-footer">
      <!-- <div class="col-md-12"> -->
      <authorCard></authorCard>
      <div class="d-flex align-items-center">
        <p class="mr-2 h4 m-0">Kategorie:</p>
        <select class="form-control form-control-lg">
          <option>Hlavní jídlo</option>
          <option>Moučník</option>
          <option>Polévka</option>
        </select>
      </div>
      <button
        type="submit"
        class="btn btn-primary h-75"
        @click="uploadToServer"
      >
        Vložit
      </button>
      <!-- </div> -->
    </div>

    <!-- </form> -->
  </div>
</template>
<script>
// import axios from 'axios';
import AuthorCard from '@/components/Author.vue';
import PhotoHolder from '@/components/PhotoHolder.vue';
import ApiService from '@/services/ApiService';

// const URL = 'http://localhost:5000/recipes/';

export default {
  name: 'NewRecipe',
  components: { AuthorCard, PhotoHolder },

  data() {
    return {
      active: false,
      name: '',
      imageFile: null,
      previewImage: undefined,
      reader: undefined,
      // placeholderURL: '@/assets/image-placeholder.jpg',
    };
  },
  created() {
    this.reader = new FileReader();
    this.reader.onload = (e) => {
      this.previewImage = e.target.result;
    };
  },
  computed: {
    imageUrl() {
      const placeholderURL = require('@/assets/image-placeholder.jpg'); // eslint-disable-line
      if (!this.previewImage) {
        return placeholderURL;
      }
      return this.previewImage;
    },
  },
  methods: {
    fileSelected(event) {
      // this.imageFile = event.target.files[0];
      [this.imageFile] = event.target.files;

      this.reader.readAsDataURL(this.imageFile);
    },
    uploadToServer() {
      const formdata = new FormData();
      formdata.append('recipeImage', this.imageFile, this.imageFile.name);
      formdata.append('title', this.name);

      ApiService.uploadNewRecipe(localStorage.token, formdata)
        .then((response) => {
          const recipe = response.data;
          this.$emit('addRecipe', recipe);
          this.active = false;
          this.name = '';
          this.file = null;
          this.previewImage = null;
        })
        .catch((err) => {
          console.log(err);
        });

      // fetch(URL, {
      //   method: 'POST',
      //   body: formdata,
      //   headers: {
      //     Authorization: `Bearer ${localStorage.token}`,
      //   },
      // })
      //   .then((response) => response.json())
      //   .then((result) => {
      //     console.log(result);
      //     this.$emit('addRecipe', result);
      //     this.active = false;
      //     this.name = '';
      //     this.file = null;
      //     this.previewImage = null;
      //   });
    },
  },
};
</script>
