<template>
  <section>
    <h1>Recepty</h1>
    <h5>helou {{ user.username }}</h5>
    <form @submit.prevent="postRecipe">
      <div class="form-group">
        <label for="title">Název receptu:</label>
        <input
          type="text"
          class="form-control"
          id="title"
          placeholder="Zadejte název.."
          v-model="recipeToInsert.title"
        />
      </div>
      <div class="form-group">
        <label for="directions">Postup:</label>
        <textarea
          v-model="recipeToInsert.directions"
          class="form-control"
          id="directions"
          rows="3"
        ></textarea>
      </div>
      <button type="submit" class="btn btn-primary">Uložit recept</button>
    </form>
    <br />

    <div
      v-for="recipe in recipes"
      :key="recipe._id"
      class="card text-white bg-primary mb-3"
      style="max-width: 20rem;"
    >
      <div class="card-body">
        <h4 class="card-title">{{ recipe.title }}</h4>
        <p class="card-text">{{ recipe.directions }}</p>
      </div>
    </div>

    <button class="btn btn-primary btn-lg" @click="logout">Odhlásit se</button>
  </section>
</template>

<script>
const API_URL = 'http://localhost:5000/';

export default {
  name: 'Dashboard',

  data() {
    return {
      user: {},
      recipeToInsert: {
        title: '',
        directions: '',
      },
      recipes: [],
    };
  },
  mounted() {
    console.log('posílám request na "/"');
    fetch(API_URL, {
      headers: {
        Authorization: `Bearer ${localStorage.token}`,
      },
    })
      .then((result) => result.json())
      .then((response) => {
        console.log('odpověď přišla v pohodě');
        console.log(response);
        this.user = response.user;
        this.getRecipes();
      });
  },
  methods: {
    logout() {
      localStorage.removeItem('token');
      this.$router.push('/login');
    },
    getRecipes() {
      const url = `${API_URL}recipes`;
      console.log('pokračuji GET requestem na /recipes/');
      fetch(url, {
        headers: {
          Authorization: `Bearer ${localStorage.token}`,
        },
      })
        .then((res) => {
          console.log('obdrželi jsme response');
          return res.json();
        })
        .then((result) => {
          console.log('tady si ji vypíšeme');
          console.log(result);
          this.recipes = result;
        });
    },
    postRecipe() {
      const url = `${API_URL}recipes`;
      fetch(url, {
        method: 'POST',
        body: JSON.stringify(this.recipeToInsert),
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.token}`,
        },
      })
        .then((response) => response.json())
        .then((result) => this.recipes.push(result));
    },
  },
};
</script>

<style lang="css" scoped></style>
