      );
      commentedRecipe.comments.push(comment);

      console.log(this.recipes[0].comments);
      console.log('created end');
    });

    console.log('posílám request na "/"');
    ApiService.authorize(localStorage.token)
      .then((response) => {
        if (response.data.user) {
          this.user = response.data.user;
          this.getRecipes();
        }
      })
      .catch((error) => {
        alert(error.response.data.message);
        this.logout();
      });

    // fetch(API_URL, {
