      ApiService.getRecipes(localStorage.token)
        .then((response) => {
          this.recipes = response.data;
        })
        .catch((error) => {
          console.log('There was an error:', error.response);
        });
    },
    openComments(recipeId) {
      if (this.openForComments !== recipeId) {
        this.openForComments = recipeId;
      } else {
        this.openForComments = '';
      }
    },
    removeRecipe(recipeId) {
      this.recipes = this.recipes.filter((recipe) => recipe._id !== recipeId); //eslint-disable-line
    },
