<template>
  <section>
    <h1>Přihlášení</h1>
    <form @submit.prevent="login">
      <div v-if="errorMessage" class="alert alert-danger" role="alert">
        {{ errorMessage }}
      </div>
      <div class="form-group">
        <label for="username">Uživatelské jméno</label>
        <input
          type="tet"
          class="form-control"
          id="username"
          v-model="user.username"
          aria-describedby="usernameHelp"
          placeholder="Zadejte uživatelské jméno"
          required
        />
      </div>
      <div class="form-group">
        <label for="password">Heslo</label>
        <input
          type="password"
          v-model="user.password"
          class="form-control"
          id="password"
          placeholder="Zadejte heslo"
          required
        />
      </div>
      <button type="submit" class="btn btn-primary">Potvrdit</button>
    </form>
  </section>
</template>
<script>
import Joi from 'joi';
import ApiService from '@/services/ApiService';

// const LOGIN_URL = 'http://localhost:5000/auth/login/';

// opakuje se - vložit do samostatného modulu
const schema = Joi.object({
  username: Joi.string()
    .pattern(new RegExp('^[a-zA-Z0-9áéíóúýčďěňřšťžůÁÉÍÓÚÝČĎĚŇŘŠŤŽŮ ]*$'))
    .min(3)
    .max(30)
    .required(),
  password: Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$')),
});

export default {
  name: 'LogIn',

  data() {
    return {
      user: {
        username: '',
        password: '',
      },
      errorMessage: '',
    };
  },
  methods: {
    login() {
      if (this.isValid()) {
        // wtf?
        const body = {
          username: this.user.username,
          password: this.user.password,
        };
        // const options = {
        //   method: 'POST',
        //   body: JSON.stringify(body),
        //   headers: {
        //     'Content-Type': 'application/json',
        //   },
        // };
        // console.log(`posílám request s následujícím nastavením: ${options}`);

        // ApiService.logIn(this.user)
        ApiService.logIn(body)
          .then((response) => {
            localStorage.token = response.data.token;
            this.$router.push('/');
          })
          .catch((error) => {
            this.errorMessage = error.message;
          });

        // fetch(LOGIN_URL, options)
        //   .then((response) => {
        //     if (response.ok) {
        //       console.log('odpověď přišla v pořádku');
        //       return response.json();
        //     }
        //     return response.json().then((error) => {
        //       console.log('je to v řiťi');
        //       throw new Error(error.message);
        //     });
        //   })
        //   .then((result) => {
        //     console.log(`právě přišel token: ${result.token}`);
        //     console.log('ukládám token');
        //     localStorage.token = result.token;
        //     console.log('token uložen - přesměrovávám na /dashboard');
        //     this.$router.push('/');
        //   })
        //   .catch((error) => {
        //     this.errorMessage = error.message;
        //   });
      }
    },
    // opakujicí se kód - refactoring DRY
    isValid() {
      const result = schema.validate(this.user);
      if (result.error === undefined) {
        return true;
      }
      if (result.error.message.includes('username')) {
        this.errorMessage = 'uživatelské jméno zadáno nesprávně';
      }
      if (result.error.message.includes('password')) {
        this.errorMessage = 'Vaše heslo bylo zadáno nesprávně';
      }
      return false;
    },
  },
  watch: {
    user: {
      handler() {
        this.errorMessage = null;
      },
      deep: true,
    },
  },
};
</script>
