<template>
  <div class="c-row">
    <img :src="imgPath" :alt="this.comment.author.avatar_img" class="avatar" />
    <div class="pl-2 buble">
      <div class="d-flex">
        <h5 class="author-name">{{ comment.author.username }}</h5>
        <p class="date-time">- {{ comment.dateTime | moment('calendar') }}:</p>
        <i class="close-icon" @click="removeComment(comment._id)"
          ><closeIcon :size="12" fillColor="#777"
        /></i>
      </div>
      <p class="comment">{{ comment.comment }}</p>
    </div>
  </div>
</template>

<script>
import CloseIcon from 'vue-material-design-icons/Close.vue';
import ApiService from '@/services/ApiService';

export default {
  name: 'Comment',

  data() {
    return {};
  },
  components: { CloseIcon },
  props: ['comment', 'recipe'],
  computed: {
    imgPath() {
      return require(`@/assets/profile_photos/${this.comment.author.avatar_img}`); // eslint-disable-line
    },
  },
  methods: {
    removeComment(commentID) {
      const recipeId = this.recipe._id; // eslint-disable-line
      console.log(
        `Mažu komentář s ID: ${commentID}, náležící receptu ${this.recipe._id}`, // eslint-disable-line
      );
      ApiService.removeComment(localStorage.token, recipeId, commentID)
        .then(() => {
          // eslint-disable-next-line
          this.recipe.comments = this.recipe.comments.filter((comment) => {
            return comment._id !== commentID; // eslint-disable-line
          });
        })
        .catch((err) => alert(err));
    },
  },
};
</script>

