import axios from 'axios';

const baseURL = process.env.VUE_APP_API_URL;

const apiClient = axios.create({
  baseURL,
  withCredentials: false, // This is the default
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json',
  },
});

export default {
  getRecipes(token) {
    const headers = {
      Authorization: `Bearer ${token}`,
    };
    return apiClient.get('/recipes', { headers });
  },
  addComment(token, recipeID, payload) {
    const headers = {
      Authorization: `Bearer ${token}`,
    };
    return apiClient.post(`/recipes/${recipeID}/comments`, payload, {
      headers,
    });
  },
  removeRecipe(token, recipeID) {
    const headers = {
      Authorization: `Bearer ${token}`,
    };
    return apiClient.delete(`/recipes/${recipeID}`, { headers });
  },
  removeComment(token, recipeId, commentId) {
    const headers = {
      Authorization: `Bearer ${token}`,
    };
    return apiClient.delete(`/recipes/${recipeId}/comments/${commentId}`, {
      headers,
    });
  },
  uploadNewRecipe(token, formdata) {
    const headers = {
      Authorization: `Bearer ${token}`,
    };
    return apiClient.post('/recipes/', formdata, { headers });
  },
  // přendat do samostatneho js file (auth.js)
  signUp(user) {
    return apiClient.post('/auth/signup/', user);
  },
  logIn(user) {
    return apiClient.post('/auth/login/', user);
  },
  authorize(token) {
    const headers = {
      Authorization: `Bearer ${token}`,
    };
    return apiClient.get('/', { headers });
  },
};
